<template>
  <div>
    <!-- 顶部导航 -->
    <Layout>
      <a href="https://www.mi.com/a/h/7095.html?client_id=180100041086&masid=17490.0435">
        <Header class="site-bn-bar">
        </Header>
      </a>
      <Footer>
        <div class="container">

          <div class="topbar-nav">
            <a :href="item.href" v-for="item in TopBarNav" :key="item.id" @click="alert('11')">
              <span class="topbar-nav-name">{{item.name}}</span>
              <span class = "sep">|</span>
            </a>
          </div>
          <div class="topbar-cart">
            <a class="cart-mini" style="font-size:12px">
              <Icon type="ios-cart" />
              购物车
              <span class="cart-mini-num">(0)</span>
            </a>
          </div>
          <div class="topbar-info"></div>
        </div>
      </Footer>
    </Layout>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import './HomePage.scss'
export default {
  computed: {
    ...mapGetters(['TopBarNav'])
  }
}
</script>



